 <template>
  <div class="card mb-3">
    <div class="row no-gutters">
      <div class="col-md-4">
        <img
          :src="user.image | emptyImage"
          width="300px"
          height="300px"
        />
      </div>
      <div class="col-md-8">
        <div class="card-body">
          <h5 class="card-title">{{ user.name }}</h5>
          <p class="card-text">{{ user.email }}</p>
          <ul class="list-unstyled list-inline">
            <li>
              <strong>{{ user.commentsLength }}</strong> 已評論餐廳
            </li>
            <li>
              <strong>{{ user.favoritedRestaurantsLength }}</strong>
              收藏的餐廳
            </li>
            <li>
              <strong>{{ user.followingsLength }}</strong> followings
              (追蹤者)
            </li>
            <li>
              <strong>{{ user.followersLength }}</strong> followers
              (追隨者)
            </li>
          </ul>
          <p>
            <router-link :to="{ name: 'user-profile-edit' }">
              <template v-if="currentUser.id === user.id">
                <button type="submit" class="btn btn-primary">edit</button>
              </template>
              <template v-else>
                <button
                  @click.prevent.stop="unfollow(user.id)"
                  v-if="user.isFollowed"
                  type="button"
                  class="btn btn-danger"
                >
                  取消追蹤
                </button>
                <button
                  v-else
                  @click.prevent.stop="follow(user.id)"
                  type="button"
                  class="btn btn-primary"
                >
                  追蹤
                </button>
              </template>
            </router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { btns, emptyImageFilter } from "../../utils/mixins";
export default {
  mixins: [btns, emptyImageFilter],
  props: {
    userProfile: {
      type: Object,
      required: true,
    },
    currentUser: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {
      user: this.userProfile,
    };
  },
  watch: {
    userProfile(newValue) {
      this.user = {
        ...this.user,
        ...newValue,
      };
    },
  },
};
</script>